.contenedor-testimonio{
    background-color: #fff;
    width: 1170px;
    height: 368px;
    margin: 10px 10px 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    box-shadow: 0 3px 13px 1px rgb(0, 0, 0,9%);
    border-radius: 10px;
}

.imagen-testimonio{
    height: 100%;
    width: auto;
    border-top-left-radius: 10px;
    border-bottom-left-radius: 10px;
}

.contenedor-texto-testimonio{
    text-align: center;
    padding: 40px;
    font-family: Lato,sans-serif;
    line-height: 1.5rem;
    font-weight: 400;
    border-radius: 10px;

}

.nombre-testimonio{
    font-size: 1.3rem;
}

.cargo-testimonio{
    font-size: 1.3rem;
    margin-top: 15px;
    padding-bottom: 30px;
}

.texto-testimonio{
    font-size: 1.1rem;
    text-align: justify;
}